'use strict';


var Chan = require("./Chan");
/**
 *CrossCall 
 *worker windowdocument 
 *
 * @method
 *
 * @param {function}   {id:32, text:"I`m Tester"}
 */
function CrossCall( fun , param){
    this._fun = fun;
    this._param = param;
}

/**
 *
 *
 * @method
 *
 * @param {function}  {id:32, text:"I`m Tester"}
 */
CrossCall.prototype.setFun = function ( fun , param) {
	if( typeof fun !=="function" ) return;
	this._fun  = fun;
    this._param = param;
	return this;
}

/**
 *
 *
 * @method
 *
 * @param {function} _fun
 */
CrossCall.prototype.exec = function( fun ) {

		Chan.add( this._fun, fun , this._param);
};

module.exports = CrossCall;