function Ajax( url ) {
	if( typeof url !== 'string' )
		throw new Error( 'Ajax option error!' );

	//  
	this.url = !~url.indexOf( 'http' ) ? config.ajax_base + '/v' + config.version + url.replace( /^\/*/,'/' ) : url;
}

// Ajax 
Ajax.prototype.config = function( cfg ){
	this.cfg = cfg;
	return this;
}

// Ajax 
Ajax.prototype.router = function( router ){

	if( !router )
		throw new Error( 'Router' );

	if( router.constructor.name === 'String' )
		router = '/' + router;
	else
		router = '/' + router.join( '/' );

	var aUrl = this.url.split( '?' );
	this.url = aUrl[0].replace( /\/*$/, router );
	aUrl[1] && (this.url = this.url + aUrl[1]);

	this.r = router;
	return this;
}

// Ajax data
Ajax.prototype.data = function( data ){
	var sData = [];
	for( var i in data )
		sData.push( i + '=' + data[i] );
	sData =  '&' + sData.join( '&' );

	this.d = sData;
	return this;
}

// Ajax ?
Ajax.prototype.path = function ( path ){

	var sPath = [];
	for( var i in path )
		sPath.push( i + '=' + path[i] );
	sPath =  sPath.join( '&' );

	if( !~this.url.indexOf( '?' ) )
		this.url = this.url.replace( /\/*$/, '?' + sPath )
	else
		this.url = this.url.replace( /[&\/]*$/, '&' + sPath );

	this.p = sPath;
	return this;
}

// Ajax 
Ajax.prototype.success = function( fn ) {
	if( !fn || typeof fn  !== 'function' )
		throw new Error( '!' );

	this.s = fn;
	return this;
}

// Ajax 
Ajax.prototype.error = function( fn ){
	if( !fn || typeof fn  !== 'function' )
		throw new Error( '!' );

	this.e = fn;
	return this;
}

// 
Ajax.prototype.timeout = function( fn ){
	if( !fn || typeof fn  !== 'function' )
		throw new Error( '!' );

	this.t = fn;
	return this;
}

// get 
Ajax.prototype.get = function(){
	this.m = 'GET';
	this.exec();
}

// post 
Ajax.prototype.post = function(xx){
	this.m = 'POST';
	this.exec(xx);
}

// put 
Ajax.prototype.put = function(){
	this.m = 'PUT';
	this.exec();
}

// delete 
Ajax.prototype.delete = function(){
	this.m = 'DELETE';
	this.exec();
}

Ajax.prototype.exec = function(xx){

	this.cfg = this.cfg || {};

	var xhr = new XMLHttpRequest();

	// 3000
	xhr.timeout =  this.cfg.timeout || 3000;

	//   true
	if(xx == false){
		this.async = false;
	}else{
		this.async = typeof this.cfg.async === 'boolean' ? this.cfg.async : true;
	}
	

	xhr.open( this.m, this.url, this.async );

	if( this.m === 'GET' )
		xhr.send( null );
	else{
		xhr.setRequestHeader( 'Content-Type', 'application/x-www-form-urlencoded' );
		xhr.send( this.d );
	}

	xhr.ontimeout = this.t;

	if( !this.async )
	  callback( xhr, this );
	else
		xhr.onreadystatechange = function(){
			if( xhr.readyState === 4 )
				callback( xhr, this );
		}.bind( this )
}

function callback( xhr, obj ){
	var result = '';
	if( xhr.status == 200 ){
		try{
			result = JSON.parse( xhr.response );
		}catch(e){
			result = xhr.response;
		}
		obj.s( result );
	}else 
		obj.e && obj.e( xhr );
}


module.exports = Ajax;